import React from 'react';
import { NavbarBase, Item } from './styled';
const Component = ({ items, setValueFilter }) => {

    const renderItems = () => {
        items.map((item, index) => <Item key={`${index}-item`} onClick={() => setValueFilter(item.value)}>{item.name}</Item>)
    }
    return (
        <NavbarBase>
            {renderItems}
        </NavbarBase>
    );
}

export default Component;
export { Component as NavbarBase }